import EventEmitter from "./lib/event_emitter";

export class Sender extends EventEmitter {
    callback(data: any, callbackId: any) {
        postMessage({
            type: "call",
            id: callbackId,
            data: data
        });
    };

    emit(name: any, data: any) {
        postMessage({
            type: "event",
            name: name,
            data: data
        });
    };
}