"use strict";

import EventEmitter from "./lib/event_emitter";
import Anchor from "./anchor";
import Range from './lib/range';

const applyDelta = require("./apply_delta").applyDelta;

class Document extends EventEmitter {
    $autoNewLine = "";
    $newLineMode = "auto";
    private readonly $lines: string[];

    constructor(textOrLines: any) {
        super();
        this.$lines = [""];
        if (textOrLines.length === 0) {
            this.$lines = [""];
        } else if (Array.isArray(textOrLines)) {
            this.insertMergedLines({row: 0, column: 0}, textOrLines);
        } else {
            this.insert({row: 0, column: 0}, textOrLines);
        }
    }

    setValue(text: string) {
        const len = this.getLength() - 1;
        this.remove(new Range(0, 0, len, this.getLine(len).length));
        this.insert({row: 0, column: 0}, text);
    };

    getValue() {
        return this.getAllLines().join(this.getNewLineCharacter());
    };

    createAnchor(row: number, column: number) {
        return new Anchor(this, row, column);
    };

    $split(text: string) {
        if ("aaa".split(/a/).length === 0) {
            return text.replace(/\r\n|\r/g, "\n").split("\n");
        }

        return text.split(/\r\n|\r|\n/);
    }


    $detectNewLine(text: string) {
        const match = text.match(/^.*?(\r\n|\r|\n)/m);
        this.$autoNewLine = match ? match[1] : "\n";
        this._signal("changeNewLineMode");
    }

    getNewLineCharacter() {
        switch (this.$newLineMode) {
            case "windows":
                return "\r\n";
            case "unix":
                return "\n";
            default:
                return this.$autoNewLine || "\n";
        }
    };

    setNewLineMode(newLineMode: string) {
        if (this.$newLineMode === newLineMode)
            return;

        this.$newLineMode = newLineMode;
        this._signal("changeNewLineMode");
    };

    getNewLineMode() {
        return this.$newLineMode;
    };

    isNewLine(text: string) {
        return (text === "\r\n" || text === "\r" || text === "\n");
    };

    getLine(row: number) {
        return this.$lines[row] || "";
    };

    getLines(firstRow: number, lastRow: number) {
        return this.$lines.slice(firstRow, lastRow + 1);
    };

    getAllLines() {
        return this.getLines(0, this.getLength());
    };

    getLength() {
        return this.$lines.length;
    };

    getTextRange(range: any) {
        return this.getLinesForRange(range).join(this.getNewLineCharacter());
    };

    getLinesForRange(range: { start: any; end: any; }) {
        let lines;
        if (range.start.row === range.end.row) {
            lines = [this.getLine(range.start.row).substring(range.start.column, range.end.column)];
        } else {
            lines = this.getLines(range.start.row, range.end.row);
            lines[0] = (lines[0] || "").substring(range.start.column);
            const l = lines.length - 1;
            if (range.end.row - range.start.row === l)
                lines[l] = lines[l].substring(0, range.end.column);
        }
        return lines;
    };

    insertLines(row: any, lines: any) {
        console.warn("Use of document.insertLines is deprecated. Use the insertFullLines method instead.");
        return this.insertFullLines(row, lines);
    };

    removeLines(firstRow: any, lastRow: any) {
        console.warn("Use of document.removeLines is deprecated. Use the removeFullLines method instead.");
        return this.removeFullLines(firstRow, lastRow);
    };

    insertNewLine(position: any) {
        console.warn("Use of document.insertNewLine is deprecated. Use insertMergedLines(position, ['', '']) instead.");
        return this.insertMergedLines(position, ["", ""]);
    };

    insert(position: { row: number; column: number; }, text: string) {
        if (this.getLength() <= 1)
            this.$detectNewLine(text);

        return this.insertMergedLines(position, this.$split(text));
    };

    insertInLine(position: { row: any; column: any; }, text: string | any[]) {
        const start = this.clippedPos(position.row, position.column);
        const end = this.pos(position.row, position.column + text.length);

        this.applyDelta({
            start: start,
            end: end,
            action: "insert",
            lines: [text]
        }, true);

        return this.clonePos(end);
    };

    clippedPos(row: number, column: number) {
        const length = this.getLength();
        if (row === undefined) {
            row = length;
        } else if (row < 0) {
            row = 0;
        } else if (row >= length) {
            row = length - 1;
            column = undefined;
        }
        const line = this.getLine(row);
        if (column === undefined)
            column = line.length;
        column = Math.min(Math.max(column, 0), line.length);
        return {row: row, column: column};
    };

    clonePos(pos: { row: any; column: any; }) {
        return {row: pos.row, column: pos.column};
    };

    pos(row: any, column: number) {
        return {row: row, column: column};
    };

    $clipPosition(position: { row: number; column: number; }) {
        const length = this.getLength();
        if (position.row >= length) {
            position.row = Math.max(0, length - 1);
            position.column = this.getLine(length - 1).length;
        } else {
            position.row = Math.max(0, position.row);
            position.column = Math.min(Math.max(position.column, 0), this.getLine(position.row).length);
        }
        return position;
    };

    insertFullLines(row: number, lines: string[]) {
        row = Math.min(Math.max(row, 0), this.getLength());
        let column;
        if (row < this.getLength()) {
            lines = lines.concat([""]);
            column = 0;
        } else {
            lines = [""].concat(lines);
            row--;
            column = this.$lines[row].length;
        }
        this.insertMergedLines({row: row, column: column}, lines);
    };

    insertMergedLines(position: { row: any; column: any; }, lines: string | any[]) {
        const start = this.clippedPos(position.row, position.column);
        const end = {
            row: start.row + lines.length - 1,
            column: (lines.length === 1 ? start.column : 0) + lines[lines.length - 1].length
        };

        this.applyDelta({
            start: start,
            end: end,
            action: "insert",
            lines: lines
        }, false);

        return this.clonePos(end);
    };

    remove(range: Range) {
        const start = this.clippedPos(range.start.row, range.start.column);
        const end = this.clippedPos(range.end.row, range.end.column);
        this.applyDelta({
            start: start,
            end: end,
            action: "remove",
            lines: this.getLinesForRange({start: start, end: end})
        }, false);
        return this.clonePos(start);
    };

    removeInLine(row: number, startColumn: number, endColumn: number) {
        const start = this.clippedPos(row, startColumn);
        const end = this.clippedPos(row, endColumn);

        this.applyDelta({
            start: start,
            end: end,
            action: "remove",
            lines: this.getLinesForRange({start: start, end: end})
        }, true);

        return this.clonePos(start);
    };

    removeFullLines(firstRow: number, lastRow: number) {
        firstRow = Math.min(Math.max(0, firstRow), this.getLength() - 1);
        lastRow = Math.min(Math.max(0, lastRow), this.getLength() - 1);
        const deleteFirstNewLine = lastRow === this.getLength() - 1 && firstRow > 0;
        const deleteLastNewLine = lastRow < this.getLength() - 1;
        const startRow = (deleteFirstNewLine ? firstRow - 1 : firstRow);
        const startCol = (deleteFirstNewLine ? this.getLine(startRow).length : 0);
        const endRow = (deleteLastNewLine ? lastRow + 1 : lastRow);
        const endCol = (deleteLastNewLine ? 0 : this.getLine(endRow).length);
        const range = new Range(startRow, startCol, endRow, endCol);
        const deletedLines = this.$lines.slice(firstRow, lastRow + 1);

        this.applyDelta({
            start: range.start,
            end: range.end,
            action: "remove",
            lines: this.getLinesForRange(range)
        }, false);
        return deletedLines;
    };

    removeNewLine(row: number) {
        if (row < this.getLength() - 1 && row >= 0) {
            this.applyDelta({
                start: this.pos(row, this.getLine(row).length),
                end: this.pos(row + 1, 0),
                action: "remove",
                lines: ["", ""]
            }, false);
        }
    };

    replace(range: Range, text: string) {
        if (text.length === 0 && range.isEmpty())
            return range.start;
        if (text === this.getTextRange(range))
            return range.end;

        this.remove(range);
        let end;
        if (text) {
            end = this.insert(range.start, text);
        } else {
            end = range.start;
        }

        return end;
    };

    applyDeltas(deltas: string | any[]) {
        for (let i = 0; i < deltas.length; i++) {
            this.applyDelta(deltas[i], false);
        }
    };

    revertDeltas(deltas: string | any[]) {
        for (let i = deltas.length - 1; i >= 0; i--) {
            this.revertDelta(deltas[i]);
        }
    };

    applyDelta(delta: { start: any; end: any; action: any; lines: any; }, doNotValidate: boolean) {
        const isInsert = delta.action === "insert";
        if (isInsert ? delta.lines.length <= 1 && !delta.lines[0]
            : !Range.comparePoints(delta.start, delta.end)) {
            return;
        }

        if (isInsert && delta.lines.length > 20000)
            this.$splitAndapplyLargeDelta(delta, 20000);
        applyDelta(this.$lines, delta, doNotValidate);
        this._signal("change", delta);
    };

    $splitAndapplyLargeDelta(delta: { start: any; end?: any; action: any; lines: any; }, MAX: number) {
        const lines = delta.lines;
        const l = lines.length;
        const row = delta.start.row;
        let column = delta.start.column;
        let from = 0, to = 0;
        do {
            from = to;
            to += MAX - 1;
            const chunk = lines.slice(from, to);
            if (to > l) {
                delta.lines = chunk;
                delta.start.row = row + from;
                delta.start.column = column;
                break;
            }
            chunk.push("");
            this.applyDelta({
                start: this.pos(row + from, column),
                end: this.pos(row + to, column = 0),
                action: delta.action,
                lines: chunk
            }, true);
        } while (true);
    };

    revertDelta(delta: { start: { row: any; column: any; }; end: { row: any; column: any; }; action: string; lines: string | any[]; }) {
        this.applyDelta({
            start: this.clonePos(delta.start),
            end: this.clonePos(delta.end),
            action: (delta.action === "insert" ? "remove" : "insert"),
            lines: delta.lines.slice()
        }, false);
    };

    indexToPosition(index: number, startRow: number) {
        const lines = this.$lines || this.getAllLines();
        const newlineLength = this.getNewLineCharacter().length;
        let i: number, l: number;
        for (i = startRow || 0, l = lines.length; i < l; i++) {
            index -= lines[i].length + newlineLength;
            if (index < 0)
                return {row: i, column: index + lines[i].length + newlineLength};
        }
        return {row: l - 1, column: lines[l - 1].length};
    };

    positionToIndex(pos: { row: number; column: number; }, startRow: number) {
        const lines = this.$lines || this.getAllLines();
        const newlineLength = this.getNewLineCharacter().length;
        let index = 0;
        const row = Math.min(pos.row, lines.length);
        for (let i = startRow || 0; i < row; ++i)
            index += lines[i].length + newlineLength;

        return index + pos.column;
    };
}


export default Document;