import {ANTLRErrorListener} from "antlr4ts";

/** Gathers errors and posts them to ACE editor
 */
export class AnnotatingErrorListener implements ANTLRErrorListener<any> {
    annotations: {
        row: number,
        column: number,
        text: string,
        type: string,
    }[];

    constructor(annotations: {
        row: number,
        column: number,
        text: string,
        type: string,
    }[]) {
        this.annotations = annotations;
    }

    syntaxError(recognizer: any, offendingSymbol: any, line: number, column: any, msg: any, e: any) {
        this.annotations.push({
            row: line - 1,
            column: column,
            text: msg,
            type: "error"
        });
    }
}