import {Java8ParserListener} from "../java8/Java8ParserListener";
import {
    AnnotationContext,
    BlockContext,
    ClassDeclarationContext,
    FieldDeclarationContext,
    ImportDeclarationContext,
    Java8Parser,
    LocalVariableDeclarationContext,
    MethodBodyContext,
    MethodDeclarationContext,
    MethodHeaderContext,
    PackageDeclarationContext,
    PackageNameContext,
    PrimaryContext,
    TypeNameContext
} from "../java8/Java8Parser";
import {Java8Lexer} from "../java8/Java8Lexer";

export class AceJavaListener implements Java8ParserListener {
    imports: any[];
    typesRequired: any[];
    variables: any[];
    methods: Map<string, {
        type: string,
        name: string,
        modifier: string,
        params: string[]
    }[]>;
    scope: {
        nextId: number;
        parent: any[]; current: { level: number; id: number, start: number, stop: number, parent: any };
    };
    className: string;
    packageName: string;
    interfaceNames: any[];
    parentClassName: string;

    constructor() {
        this.imports = [];
        this.typesRequired = [];
        this.variables = [];
        this.methods = new Map();
        this.scope = {
            current: {
                level: 0,
                id: 0,
                start: 0,
                stop: 0,
                parent: null
            },
            nextId: 1,
            parent: []
        };
        this.scope.parent.push(this.scope.current);
        this.packageName = null;
        this.className = null;
        this.parentClassName = null;
        this.interfaceNames = [];
    }

    exitPackageDeclaration(ctx: PackageDeclarationContext) {
        for (let i = 0; i < ctx.children.length; i++) {
            const child = ctx.children[i];
            if (child instanceof PackageNameContext) {
                this.packageName = child.text;
                break;
            }
        }
    }

    exitImportDeclaration(ctx: ImportDeclarationContext) {
        let cImport = ctx.text.substr('import'.length);
        cImport = cImport.substr(0, cImport.length - 1);
        this.imports.push(cImport);
    }

    exitClassDeclaration(ctx: ClassDeclarationContext) {
        if (this.scope.current.level !== 0) return;
        let normalClass = ctx.normalClassDeclaration();
        if (!normalClass) {
            console.warn("top most class isn't a normal class, not yet implemented");
        }
        this.className = normalClass.getToken(Java8Parser.Identifier, 0).text;
        if (normalClass.superclass()) {
            this.parentClassName = normalClass.superclass().classType().text;
        }

        if (normalClass.superinterfaces()) {
            this.interfaceNames = normalClass.superinterfaces().interfaceTypeList().interfaceType().map(x => x.text);
        }
    }

    exitTypeName(ctx: TypeNameContext) {
        this.typesRequired.push(ctx.text);
    }

    exitAnnotationName(ctx: TypeNameContext) {
        const text = ctx.text;
        if (text === 'Override' || text === 'Deprecated') return;
        this.typesRequired.push(ctx.text);
    }

    exitPrimary(ctx: PrimaryContext) {
        const child = ctx.getChild(0);
        if (typeof child === 'undefined') return;
    }

    exitLocalVariableDeclaration(ctx: LocalVariableDeclarationContext) {
        this.handleVariableDeclaration(ctx, 'local')
    }

    exitFieldDeclaration(ctx: FieldDeclarationContext) {
        if (this.scope.current.level !== 0) return;
        this.handleVariableDeclaration(ctx, 'field')
    }

    handleVariableDeclaration(ctx: FieldDeclarationContext | LocalVariableDeclarationContext, decType: string) {
        const ids = ctx.variableDeclaratorList().variableDeclarator().map(x => x.variableDeclaratorId().text);
        const type = ctx.unannType().text;
        ids.forEach((id) => {
            this.variables.push({
                scope: this.scope.current,
                type: type,
                id: id,
                decType: decType,
                line: ctx.stop.line
            });
        });
    }

    exitMethodDeclaration(ctx: MethodDeclarationContext) {
        if (this.scope.current.level !== 0) return;
        let methodModifiers = ctx.methodModifier();
        let methodHeader: MethodHeaderContext = ctx.methodHeader();

        let securityModifierTokens = methodModifiers.filter(x => !(x instanceof AnnotationContext));
        let modifier = "";
        if (securityModifierTokens.length > 0) {
            modifier = securityModifierTokens.pop().text;
        }

        let type = methodHeader.result().text;
        let formalParameterTypes: string[] = [];
        if (methodHeader.methodDeclarator().formalParameterList()) {
            let lastFormalParameter = methodHeader.methodDeclarator().formalParameterList().lastFormalParameter();
            let formalParameters = methodHeader.methodDeclarator().formalParameterList().formalParameters();
            if (formalParameters) {
                formalParameterTypes = formalParameters.formalParameter().map((x) => x.unannType().text);
                formalParameterTypes.push(lastFormalParameter.unannType().text);
            } else {
                // we need to distinguish between a real formal parameter and a flatten almost versions
                if (lastFormalParameter.formalParameter()) {
                    formalParameterTypes = [lastFormalParameter.formalParameter().unannType().text];
                } else {
                    let unannTypeContext = lastFormalParameter.unannType();
                    formalParameterTypes = [unannTypeContext.text];
                }
            }
        }

        let name = methodHeader.methodDeclarator().Identifier().text;

        if (!this.methods.get(name)) {
            this.methods.set(name, []);
        }
        this.methods.get(name).push({
            type: type,
            name,
            modifier,
            params: formalParameterTypes
        });
    }

    enterBlock() {
        const parentScope = this.scope.current;
        this.scope.parent.push(parentScope);
        let currentId = this.scope.nextId;
        this.scope.nextId++;
        this.scope.current = {
            start: 0,
            stop: 0,
            id: currentId,
            level: this.scope.current.level + 1,
            parent: parentScope
        };
    }


    exitBlock(ctx: BlockContext) {
        this.scope.current.start = ctx.start.startIndex;
        this.scope.current.stop = ctx.stop.stopIndex;
        this.scope.current = this.scope.parent.pop();
    }

    processTokens(lexer: Java8Lexer) {
        lexer.reset();
        return lexer.getAllTokens().map((token, i) => ({
            column: token.charPositionInLine,
            line: token.line,
            start: token.startIndex,
            stop: token.stopIndex,
            type: lexer.vocabulary.getSymbolicName(token.type),
            text: token.text,
            index: i
        }));
    }
}