"use strict";

import EventEmitter from "./lib/event_emitter";

class Anchor extends EventEmitter {
    $insertRight = false;
    private $onChange: any;
    private row: number;
    private column: number;
    private document: any;

    constructor(doc: any, row: number | {row: number, column: number}, column: number) {
        super();
        this.$onChange = this.onChange.bind(this);
        this.attach(doc);

        if (typeof column === "undefined") {
            if (typeof row !== "number") {
                this.setPosition(row.row, row.column, false);
            }
        } else if (typeof row === "number") {
                this.setPosition(row, column, false);
        }
    }

    getPosition() {
        return this.$clipPositionToDocument(this.row, this.column);
    };
    getDocument() {
        return this.document;
    };
    onChange(delta: { start: { row: number; }; end: { row: any; }; }) {
        if (delta.start.row === delta.end.row && delta.start.row !== this.row)
            return;

        if (delta.start.row > this.row)
            return;

        const point = this.$getTransformedPoint(delta, {row: this.row, column: this.column}, this.$insertRight);
        this.setPosition(point.row, point.column, true);
    };

    $pointsInOrder(point1: { column: number; row: number; }, point2: { column: number; row: number; }, equalPointsInOrder: boolean) {
        const bColIsAfter = equalPointsInOrder ? point1.column <= point2.column : point1.column < point2.column;
        return (point1.row < point2.row) || (point1.row === point2.row && bColIsAfter);
    }

    $getTransformedPoint(delta: { start: any; end: any; action?: any; }, point: { row: any; column: any; }, moveIfEqual: boolean) {
        const deltaIsInsert = delta.action === "insert";
        const deltaRowShift = (deltaIsInsert ? 1 : -1) * (delta.end.row - delta.start.row);
        const deltaColShift = (deltaIsInsert ? 1 : -1) * (delta.end.column - delta.start.column);
        const deltaStart = delta.start;
        const deltaEnd = deltaIsInsert ? deltaStart : delta.end; // Collapse insert range.
        if (this.$pointsInOrder(point, deltaStart, moveIfEqual)) {
            return {
                row: point.row,
                column: point.column
            };
        }
        if (this.$pointsInOrder(deltaEnd, point, !moveIfEqual)) {
            return {
                row: point.row + deltaRowShift,
                column: point.column + (point.row === deltaEnd.row ? deltaColShift : 0)
            };
        }

        return {
            row: deltaStart.row,
            column: deltaStart.column
        };
    }

    setPosition(row: number, column: number, noClip: boolean) {
        let pos;
        if (noClip) {
            pos = {
                row: row,
                column: column
            };
        } else {
            pos = this.$clipPositionToDocument(row, column);
        }

        if (this.row === pos.row && this.column === pos.column)
            return;

        const old = {
            row: this.row,
            column: this.column
        };

        this.row = pos.row;
        this.column = pos.column;
        this._signal("change", {
            old: old,
            value: pos
        });
    };
    detach() {
        this.document.removeEventListener("change", this.$onChange);
    };

    attach(doc: any) {
        this.document = doc || this.document;
        this.document.on("change", this.$onChange);
    };

    $clipPositionToDocument(row: number, column: number) {
        let pos: { column: number; row: number } = {column: 0, row: 0};

        if (row >= this.document.getLength()) {
            pos.row = Math.max(0, this.document.getLength() - 1);
            pos.column = this.document.getLine(pos.row).length;
        }
        else if (row < 0) {
            pos.row = 0;
            pos.column = 0;
        }
        else {
            pos.row = row;
            pos.column = Math.min(this.document.getLine(pos.row).length, Math.max(0, column));
        }

        if (column < 0)
            pos.column = 0;

        return pos;
    };
}

export default Anchor