"use strict";

class Range {
    readonly start: { column: number; row: number };
    readonly end: { column: number; row: number };

    constructor(startRow: number, startColumn: number, endRow: number, endColumn: number) {
        this.start = {
            row: startRow,
            column: startColumn
        };

        this.end = {
            row: endRow,
            column: endColumn
        };
    }

    static fromPoints(start: { row: any; column: any; }, end: { row: any; column: any; }) {
        return new Range(start.row, start.column, end.row, end.column);
    }

    static comparePoints(p1: { row: number; column: number; }, p2: { row: number; column: number; }) {
        return p1.row - p2.row || p1.column - p2.column;
    }

    isEqual(range: { start: { row: number; column: number; }; end: { row: number; column: number; }; }) {
        return this.start.row === range.start.row &&
            this.end.row === range.end.row &&
            this.start.column === range.start.column &&
            this.end.column === range.end.column;
    };

    toString() {
        return ("Range: [" + this.start.row + "/" + this.start.column +
            "] -> [" + this.end.row + "/" + this.end.column + "]");
    };

    contains(row: number, column: number) {
        return this.compare(row, column) === 0;
    };

    compareRange(range: Range) {
        let cmp;
        const end = range.end,
            start = range.start;

        cmp = this.compare(end.row, end.column);
        if (cmp === 1) {
            cmp = this.compare(start.row, start.column);
            if (cmp === 1) {
                return 2;
            } else if (cmp === 0) {
                return 1;
            } else {
                return 0;
            }
        } else if (cmp === -1) {
            return -2;
        } else {
            cmp = this.compare(start.row, start.column);
            if (cmp === -1) {
                return -1;
            } else if (cmp === 1) {
                return 42;
            } else {
                return 0;
            }
        }
    };

    comparePoint(p: {row: number, column: number}) {
        return this.compare(p.row, p.column);
    };

    containsRange(range: Range) {
        return this.comparePoint(range.start) === 0 && this.comparePoint(range.end) === 0;
    };

    intersects(range: Range) {
        const cmp = this.compareRange(range);
        return (cmp === -1 || cmp === 0 || cmp === 1);
    };

    isEnd(row: number, column: number) {
        return this.end.row === row && this.end.column === column;
    };

    isStart(row: number, column: number) {
        return this.start.row === row && this.start.column === column;
    };

    setStart(row: number | {row: number, column: number}, column: number) {
        if (typeof row === "object") {
            this.start.column = row.column;
            this.start.row = row.row;
        } else {
            this.start.row = row;
            this.start.column = column;
        }
    };

    setEnd(row: number | {row: number, column: number}, column: number) {
        if (typeof row === "object") {
            this.end.column = row.column;
            this.end.row = row.row;
        } else {
            this.end.row = row;
            this.end.column = column;
        }
    };

    inside(row: number, column: number) {
        if (this.compare(row, column) === 0) {
            return !(this.isEnd(row, column) || this.isStart(row, column));
        }
        return false;
    };

    insideStart(row: number, column: number) {
        if (this.compare(row, column) === 0) {
            return !this.isEnd(row, column);
        }
        return false;
    };

    insideEnd(row: number, column: number) {
        if (this.compare(row, column) === 0) {
            return !this.isStart(row, column);
        }
        return false;
    };

    compare(row: number, column: number) {
        if (!this.isMultiLine()) {
            if (row === this.start.row) {
                return column < this.start.column ? -1 : (column > this.end.column ? 1 : 0);
            }
        }

        if (row < this.start.row)
            return -1;

        if (row > this.end.row)
            return 1;

        if (this.start.row === row)
            return column >= this.start.column ? 0 : -1;

        if (this.end.row === row)
            return column <= this.end.column ? 0 : 1;

        return 0;
    };

    compareStart(row: number, column: number) {
        if (this.start.row === row && this.start.column === column) {
            return -1;
        } else {
            return this.compare(row, column);
        }
    };

    compareEnd(row: number, column: number) {
        if (this.end.row === row && this.end.column === column) {
            return 1;
        } else {
            return this.compare(row, column);
        }
    };

    compareInside(row: number, column: number) {
        if (this.end.row === row && this.end.column === column) {
            return 1;
        } else if (this.start.row === row && this.start.column === column) {
            return -1;
        } else {
            return this.compare(row, column);
        }
    };

    clipRows(firstRow: number, lastRow: number) {
        let end: { column: number; row: number };
        if (this.end.row > lastRow)
            end = {row: lastRow + 1, column: 0};
        else if (this.end.row < firstRow)
            end = {row: firstRow, column: 0};

        let start: { column: number; row: number };
        if (this.start.row > lastRow)
            start = {row: lastRow + 1, column: 0};
        else if (this.start.row < firstRow)
            start = {row: firstRow, column: 0};

        return Range.fromPoints(start || this.start, end || this.end);
    };

    extend(row: number, column: number) {
        const cmp = this.compare(row, column);

        let start: { column: number, row: number };
        let end: { column: number, row: number };
        if (cmp === 0) {
            return this;
        } else if (cmp === -1) {
            start = {row: row, column: column};
        } else {
            end = {row: row, column: column};
        }

        return Range.fromPoints(start || this.start, end || this.end);
    };

    isEmpty() {
        return (this.start.row === this.end.row && this.start.column === this.end.column);
    };

    isMultiLine() {
        return (this.start.row !== this.end.row);
    };

    clone() {
        return Range.fromPoints(this.start, this.end);
    };

    collapseRows() {
        if (this.end.column === 0)
            return new Range(this.start.row, 0, Math.max(this.start.row, this.end.row - 1), 0);
        else
            return new Range(this.start.row, 0, this.end.row, 0)
    };

    toScreenRange(session: { documentToScreenPosition: (arg0: { column: number; row: number; }) => any; }) {
        const screenPosStart = session.documentToScreenPosition(this.start);
        const screenPosEnd = session.documentToScreenPosition(this.end);

        return new Range(
            screenPosStart.row, screenPosStart.column,
            screenPosEnd.row, screenPosEnd.column
        );
    };

    moveBy(row: number, column: number) {
        this.start.row += row;
        this.start.column += column;
        this.end.row += row;
        this.end.column += column;
    };
}

export default Range;