"use strict";

import Document from "./document";
import {Sender} from "./sender";

let lang = require("./lib/lang");

export class Mirror {
    public sender: Sender;
    public doc: Document = null;
    public $timeout: number = 500;
    private deferredUpdate: any;

    constructor(sender: Sender) {
        this.sender = sender;
        this.doc = new Document("");
        let deferredUpdate = this.deferredUpdate = lang.delayedCall(this.onUpdate.bind(this));
        let _self = this;
        sender.on("change", (e: { data: any; }) => {
            let data = e.data;
            if (data[0].start) {
                _self.doc.applyDeltas(data);
            } else {
                for (let i = 0; i < data.length; i += 2) {
                    let d;
                    if (Array.isArray(data[i + 1])) {
                        d = {action: "insert", start: data[i], lines: data[i + 1], end: undefined};
                    } else {
                        d = {action: "remove", start: data[i], end: data[i + 1], lines: undefined};
                    }
                    _self.doc.applyDelta(d, true);
                }
            }
            if (_self.$timeout)
                return deferredUpdate.schedule(_self.$timeout);
            _self.onUpdate();
        });
    }

    setTimeout(timeout: number) {
        this.$timeout = timeout;
    };

    setValue(value: string) {
        this.doc.setValue(value);
        this.deferredUpdate.schedule(this.$timeout);
    };

    getValue(callbackId: any) {
        this.sender.callback(this.doc.getValue(), callbackId);
    };

    onUpdate() {
    };

    isPending() {
        return this.deferredUpdate.isPending();
    };
}
