"use strict";

const stopPropagation = function () {
    this.propagationStopped = true;
};
const preventDefault = function () {
    this.defaultPrevented = true;
};


class EventEmitter {
    private _eventRegistry: Map<string, any>;
    private _defaultHandlers: Map<string, any>;
    private _disabled: Map<string, any[]>;

    _signal(eventName: string, e: { old?: { row: number; column: number; }; value?: { column: number; row: number; }; start?: any; end?: any; action?: any; lines?: any; }) {
        let listeners = (this._eventRegistry || new Map<string, any>()).get(eventName);
        if (!listeners)
        return;
        listeners = listeners.slice();
        for (let i = 0; i < listeners.length; i++)
        listeners[i](e, this);
    }

    _emit(eventName: string, e: any) {
        return this.dispatchEvent(eventName, e)
    }

    _dispatchEvent(eventName: string, e: any) {
        return this.dispatchEvent(eventName, e)
    }

    dispatchEvent(eventName: string, e: { type?: any; stopPropagation?: any; preventDefault?: any; propagationStopped?: any; defaultPrevented?: any; }) {
        this._eventRegistry || (this._eventRegistry = new Map<string, any>());
        this._defaultHandlers || (this._defaultHandlers = new Map<string, any>());

        let listeners = this._eventRegistry.get(eventName) || [];
        const defaultHandler = this._defaultHandlers.get(eventName);
        if (!listeners.length && !defaultHandler)
            return;

        if (typeof e !== "object" || !e)
            e = {};

        if (!e.type)
            e.type = eventName;
        if (!e.stopPropagation)
            e.stopPropagation = stopPropagation;
        if (!e.preventDefault)
            e.preventDefault = preventDefault;

        listeners = listeners.slice();
        for (let i = 0; i < listeners.length; i++) {
            listeners[i](e, this);
            if (e.propagationStopped)
                break;
        }

        if (defaultHandler && !e.defaultPrevented)
            return defaultHandler(e, this);
    };

    once(eventName: string, callback: () => void) {
        callback && this.addEventListener(eventName, function newCallback() {
            this.removeEventListener(eventName, newCallback);
            callback.apply(null, arguments);
        }, false);
    };

    setDefaultHandler(eventName: string, callback: any) {
        let handlers = this._defaultHandlers;
        if (!handlers) {
            this._defaultHandlers = new Map<string, any>();
            this._disabled = new Map<string, any[]>();
            handlers = this._defaultHandlers;
        }

        if (handlers.get(eventName)) {
            const old = handlers.get(eventName);
            let disabled = this._disabled.get(eventName);
            if (!disabled)
                this._disabled.set(eventName, []);
                disabled = this._disabled.get(eventName);
            disabled.push(old);
            const i = disabled.indexOf(callback);
            if (i !== -1)
                disabled.splice(i, 1);
        }
        handlers.set(eventName, callback);
    };

    removeDefaultHandler(eventName: string, callback: any) {
        const handlers = this._defaultHandlers;
        if (!handlers)
            return;
        const disabled = this._disabled.get(eventName);

        if (handlers.get(eventName) === callback) {
            const old = handlers.get(eventName);
            if (disabled)
                this.setDefaultHandler(eventName, disabled.pop());
        } else if (disabled) {
            const i = disabled.indexOf(callback);
            if (i !== -1)
                disabled.splice(i, 1);
        }
    };

    on(eventName: string, callback: () => void, capturing: boolean) {
        return this.addEventListener(eventName, callback, capturing);
    }

    addEventListener(eventName: string, callback: () => void, capturing: boolean) {
        this._eventRegistry = this._eventRegistry || new Map<string, any>();

        let listeners = this._eventRegistry.get(eventName);
        if (!listeners)
            listeners = [];
            this._eventRegistry.set(eventName, listeners)

        if (listeners.indexOf(callback) === -1)
            listeners[capturing ? "unshift" : "push"](callback);
        return callback;
    };

    off(eventName: any, callback: () => any) {
        return this.removeEventListener(eventName, callback)
    }


    removeListener(eventName: any, callback: () => any) {
        return this.removeEventListener(eventName, callback)
    }

    removeEventListener(eventName: string, callback: () => void) {
        this._eventRegistry = this._eventRegistry || new Map<string, any>();

        const listeners = this._eventRegistry.get(eventName);
        if (!listeners)
            return;

        const index = listeners.indexOf(callback);
        if (index !== -1)
            listeners.splice(index, 1);
    };

    removeAllListeners(eventName: string) {
        if (this._eventRegistry) this._eventRegistry.set(eventName, []);
    };
}

export default EventEmitter;